h1{font-family:'Raleway';font-weight:700 !important;color:white;font-size:3.5rem !important}p{color:white;font-weight:400}a.social{margin:0 1rem;transition:-webkit-transform 250ms;transition:transform 250ms;transition:transform 250ms, -webkit-transform 250ms;display:inline-block;color:#fff}a.social:hover{-webkit-transform:translateY(-2px);transform:translateY(-2px);color:#b9a3ff}.social-links{margin-top:2rem}.bg-photo{background-repeat:no-repeat;background-size:contain;background:linear-gradient(0deg, rgba(33,37,41,0.6), rgba(33,37,41,0.6)),url(/static/media/computer.32c16a65.jpg);background-size:cover}

html,body{padding:0;margin:0;background:#212529;font-family:Raleway;font-size:15px}#shots{display:flex;flex-wrap:wrap;max-width:1080px;margin:auto}#shots .shot{position:relative;display:block;width:25%;background:#b9a3ff;color:white;text-decoration:none}#shots .shot:hover img{opacity:.1}#shots .shot:hover .title{opacity:1}#shots .shot .title{position:absolute;width:calc(100% - 2rem);padding:1rem;opacity:0;transition:opacity .2s ease-in-out;font-size:1rem;font-weight:500;letter-spacing:1px}#shots .shot img{display:block;max-width:100%;transition:opacity .2s ease-in-out}

.navigation{position:absolute;top:0;left:0}.navigation .nav-image{height:8rem;width:auto;padding:1rem}

body{margin:0;padding:0;font-family:Raleway !important;font-weight:700 !important;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:white;background:transparent !important}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

